import React from 'react';
import nf from './svgs/404.svg';
import { useHistory } from 'react-router-dom';

export default function NotFound() {

    const history = useHistory();
    const handleClick = () => history.push('/');

    return (
        <div className="w-screen h-screen flex justify-center bg-gradient-to-br from-black to-gray-900">
            <div className="w-72 h-72 self-center flex flex-col  ">
                <img className="object-scale-down" src={nf} alt="Not Found"/>
                <a
          className="cursor-pointer px-4 tracking-wider py-2 m-auto bg-gray-800 font-bold text-white text-xs rounded-lg transition transform  duration-500 ease-in-out hover:-translate-y-1 hover:scale-110 focus:outline-none focus:ring-1 focus:ring-black focus:border-transparent hover:shadow-lg"
          onClick={handleClick}
        >
          HOME
          
        </a>
            </div>
            
        </div>
    )
}
