import React from 'react'
import Typing from 'react-typing-animation'

export default function Footer(props) {
    return (
        <div className="fixed bottom-0 p-4 w-full ">
            {/* <p className="p-4 rounded-md bg-opacity-0 bg-gray-500 text-sm font-sans font-medium text-gray-600 text-center" >{props.text}</p> */}
            <Typing className="text-center p-4 rounded-md bg-opacity-0 bg-gray-500 text-sm font-sans font-medium text-gray-600 ">
                <span>designer</span>
                <Typing.Delay ms={300} />
                <Typing.Backspace count={9} />
                <span>coder</span>
                <Typing.Delay ms={300} />
                <Typing.Backspace count={6} />
                <span>engineer</span>
                <Typing.Delay ms={300} />
                <Typing.Backspace count={9} />
                <span>developer</span>
                <Typing.Delay ms={300} />
                <Typing.Backspace count={10} />
                <span>{props.text}</span>
            </Typing>
        </div>
    )
}
